          standart: {
            label: "Standart",
            font: "400 30px 'Open Sans', sans-serif",
            stroke: false
          },
          daktilo: {
            label: "Daktilo",
            font: "400 30px 'Roboto Slab', serif",
            stroke: false
          },
          guclu: {
            label: "Güçlü",
            font: "700 30px 'Roboto', sans-serif",
            stroke: false
          }
