<template>
  <section class="stories">
    <CircleBox
      type="action"
      :closed="false"
      title="Hikaye Ekle"
      class="-m-right-15"
      @click="createStory"
    >
      <Icon name="Plus"/>
    </CircleBox>
    <CircleBox
      v-if="user.stories.length > 0"
      class="stories__item bounceIn -m-right-15"
      @click="openMyStory"
      title="Hikayen"
      :avatar="user.photo"
      :closed="false"
    />
    <transition-group
      class="stories__wrapper"
      tag="div"
      enter-active-class="bounceIn"
      leave-active-class="bounceOut"
    >
      <CircleBox
        class="stories__item bounceIn"
        v-for="(user) in users"
        :key="user.userId"
        :type="isVisible(user)"
        @close="removeUser(user.userId)"
        @click="openStory(user)"
        :avatar="user.photo"
        :title="user.name"
      />
    </transition-group>
  </section>
</template>

<script>
import { mapMutations, mapGetters, mapActions } from "vuex";

export default {
  name: "Stories",
  props: {
    value: {
      type: Array,
      default: () => []
    }
  },
  computed: {
    ...mapGetters(["user", "users"])
  },
  methods: {
    ...mapMutations(["changeModal"]),
    ...mapActions(["removeUser"]),
    isVisible(story) {
      return story.stories.every(s => {
        if (s.viewingUsers) {
          return s.viewingUsers.some(i => i == this.user.id);
        }
        return false;
      })
        ? "visible"
        : "";
    },
    createStory() {
      this.changeModal({
        name: "StoryCreate"
      });
    },
    openStory(user) {
      this.changeModal({
        name: "Story",
        props: {
          activeUser: user
        }
      });
    },
    openMyStory() {
      this.changeModal({
        name: "Profile"
      });
    }
  }
};
</script>

<style lang="scss">
.stories {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-items: center;
  overflow: auto;
  padding: 20px 0;

  &__wrapper {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
  }

  &__item {
    + .stories__item {
      margin-left: 15px;
    }
  }
}
</style>
