<template>
  <Button
    v-if="emojiVisible"
    type="radius"
    size="sm"
    @click="close"
    v-bind="$attrs"
    v-on="$listeners"
  >Tamam</Button>
  <Button v-else @click="close" v-bind="$attrs" v-on="$listeners">
    <Icon name="Close" :fill="fill" width="30" height="30"/>
  </Button>
</template>

<script>
import { mapMutations, mapGetters } from "vuex";
export default {
  name: "Close",
  props: {
    fill: {
      type: String,
      default: "#fff"
    },
    closeMethod: {
      type: Function
    }
  },
  computed: {
    ...mapGetters(["emojiVisible", "modalVisible"])
  },
  methods: {
    ...mapMutations(["toggleModal", "closeEmoji"]),
    close() {
      if (this.closeMethod && typeof this.closeMethod === "function") {
        this.closeMethod();
      } else {
        if (!this.emojiVisible) {
          this.toggleModal();
        } else {
          setTimeout(() => {
            this.closeEmoji();
          }, 200);
        }
      }
    }
  }
};
</script>

