<template>
  <div
    class="story-item"
    v-bind="$attrs"
    v-on="$listeners"
    :style="{background: (story && story.background) ? story.background : null }"
  >
    <Media
      v-if="story && story.type !== 'text'"
      :type="story.type"
      :src="story.src"
      @load="onLoad"
    />
    <div class="story-item__items" v-if="story && story.items">
      <component
        v-for="(item, key) in story.items"
        :key="key"
        :is="item.type | componentType"
        :value="item.value"
        :color="item.customProperties.color"
        :bg="item.customProperties.bg"
        :font="item.customProperties.font"
        :vertical="item.vertical"
        :isEdit="false"
      />
    </div>
    <div v-if="viewingUserVisible" class="story-item__viewing-users">
      <Icon name="Eye"/>
      <span>{{story.viewingUsers.length}}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: "StoryItem",
  props: {
    story: {
      type: Object,
      required: true
    },
    viewingUserVisible: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    onLoad(e) {
      this.$emit("load", e);
    }
  }
};
</script>

<style lang="scss">
.story-item {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  object-fit: cover;
  pointer-events: none;

  &__viewing-users {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    font-size: 0.8em;

    span {
      margin-bottom: 3px;
    }
  }
}
