<template>
  <div class="story__progress">
    <div v-for="index in count" :key="index" :class="variants(index)" ref="progress">
      <span
        class="story__progress__wait"
        @webkitAnimationEnd="animationEnd"
        @animationend="animationEnd"
        @oanimationend="animationEnd"
        :style="styles(index)"
      />
    </div>
  </div>
</template>

<script>
export default {
  name: "Progress",
  props: {
    time: {
      type: Number
    },
    pause: {
      type: Boolean,
      default: false
    },
    count: {
      type: Number,
      default: 0
    },
    activeNumber: {
      type: Number,
      default: 1
    }
  },
  data() {
    return {
      isActive: true,
      end: false
    };
  },
  watch: {
    time(newTime, oldTime) {
      if (newTime <= oldTime) {
        this.isActive = false;
      } else {
        this.isActive = true;
      }
    },
    activeNumber() {
      this.end = false;
    }
  },
  methods: {
    animationEnd() {
      if (this.end) return false;
      this.$emit("end");
      this.end = true;
    },
    styles(index) {
      if (index !== this.activeNumber) return {};
      return {
        animationName: "width",
        animationDuration: `${this.time}s`,
        animationPlayState: this.pause ? "paused" : "running"
      };
    },
    variants(index) {
      return {
        story__progress__item: true,
        "-active": this.activeNumber === index && this.isActive,
        "-end": this.activeNumber >= index || this.count === this.activeNumber
      };
    }
  }
};
</script>

<style lang="scss">
@keyframes width {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}
.story__progress {
  display: flex;
  align-items: center;

  &__item {
    height: 2px;
    background-color: rgba(255, 255, 255, 0.35);
    border-radius: 2px;
    width: 100%;
    position: relative;
    overflow: hidden;

    & + & {
      margin-left: 5px;
    }

    &.-end {
      .story__progress__wait {
        width: 100%;
      }
    }

    &.-active {
      .story__progress__wait {
        // transition: 1s linear;
      }
    }
  }

  &__wait {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    background-color: #fff;
    width: 0%;
    will-change: width;
  }
}
</style>
