<template>
  <p :class="variants">
    <mark
      :contenteditable="isEdit"
      v-bind="$attrs"
      @focus="onFocus(true)"
      @blur="onFocus(false)"
      @click.once="onClick"
      ref="el"
      :style="style"
      v-html="value || placeholder"
    />
  </p>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "TextItem",
  props: {
    value: {},
    isEdit: {
      type: Boolean,
      default: true
    },
    placeholder: {
      type: String,
      default: "Yazmak için dokunun"
    },
    clickedText: {
      type: String,
      default: "Bir şeyler yazın"
    },
    font: {
      type: String
    },
    color: {
      type: String
    },
    bg: {
      type: String,
      default: "transparent"
    },
    vertical: {
      type: String,
      default: "middle"
    }
  },
  computed: {
    ...mapGetters(["size"]),
    style() {
      return {
        font: this.font,
        color: this.color,
        background: this.bg
      };
    },
    variants() {
      return {
        "canvas-text": true,
        [`-position-${this.vertical}`]: !!this.vertical
      };
    }
  },
  watch: {
    value(newVal, oldVal) {
      if (newVal && !!newVal.match(this.clickedText)) {
        this.$emit("input", newVal.replace(oldVal, ""));
      }
    }
  },
  methods: {
    onClick(e) {
      if (!this.value || this.value === this.placeholder) {
        this.$emit("input", this.clickedText);
        this.$refs.el.addEventListener("keydown", e => {
          if (this.value === this.clickedText) {
            this.$emit("input", " ");
          }
        });
      }
    },
    onFocus(status) {
      this.focus = status;
      this.$emit("focus", status);
      if (!status) {
        const value = this.$refs.el.innerHTML;
        this.$emit("input", value);
      }
    }
  }
};
</script>

<style lang="scss">
.canvas-text {
  border: none;
  background: transparent;
  color: currentColor;
  display: block;
  font-size: 30px;
  min-width: 1px;
  min-height: 10px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
  min-width: 10px;
  min-height: 10px;

  mark {
    &:focus,
    &:active {
      outline: none;
      box-shadow: none;
      -webkit-box-shadow: none;
      box-shadow: none;
    }

    &::-moz-focus-inner {
      border: 0;
    }

    &::placeholder {
      color: currentColor;
      opacity: 1;
    }

    &:-ms-input-placeholder {
      color: currentColor;
    }

    &::-ms-input-placeholder {
      color: currentColor;
    }
  }

  &.-position {
    &-top {
      top: 100px;
    }

    &-middle {
      top: 50%;
      transform: translate(-50%, -50%);
    }
  }
}
</style>
