<template>
  <div class="story-modal">
    <Cube3d :rotateY="rotateY" v-hammer:swipe.all="onSwipe">
      <template slot="back">
        <Story
          :storyData="backUser"
          v-if="(backUser && (dimension > 1 || dimension < 1)) || tempViewFace == 'back'"
          :activeStoryIndex="activeStoryIndex"
          @next="swipeLeft"
          @prev="swipeRight"
        />
      </template>
      <template slot="left">
        <Story
          :storyData="leftUser"
          v-if="(leftUser && dimension == -1) || tempViewFace === 'left'"
          :activeStoryIndex="activeStoryIndex"
          @next="swipeLeft"
          @prev="swipeRight"
        />
      </template>
      <template slot="front">
        <Story
          :storyData="frontUser"
          v-if="(frontUser && dimension == 0) || tempViewFace === 'front'"
          :activeStoryIndex="activeStoryIndex"
          @next="swipeLeft"
          @prev="swipeRight"
        />
      </template>
      <template slot="right">
        <Story
          :storyData="rightUser"
          v-if="(rightUser && dimension == 1) || tempViewFace === 'right'"
          :activeStoryIndex="activeStoryIndex"
          @next="swipeLeft"
          @prev="swipeRight"
        />
      </template>
    </Cube3d>
  </div>
</template>

<script>
import { mapMutations, mapGetters } from "vuex";
export default {
  name: "StoryModal",
  props: {
    activeUser: {
      type: Object,
      required: true
    },
    activeStoryIndex: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      rotateY: 0,
      dimension: 0,
      currentUser: this.activeUser,
      activeUserIndex: 0,
      tempViewFace: ""
    };
  },
  computed: {
    ...mapGetters({
      user: "user",
      users: "users"
    }),
    viewUser() {
      return this.users[this.activeUserIndex];
    },
    frontUser() {
      if (this.dimension === 0) {
        return this.viewUser;
      }
      return this.users[this.activeUserIndex + this.dimension];
    },
    leftUser() {
      if (this.dimension === -1) {
        return this.viewUser;
      }
      return this.users[this.activeUserIndex - 1];
    },
    rightUser() {
      if (this.dimension === 1) {
        return this.viewUser;
      }
      return this.users[this.activeUserIndex + 1];
    },
    backUser() {
      if (this.dimension === 2 || this.dimension === -2) {
        return this.viewUser;
      }
      return this.users[this.activeUserIndex + this.dimension];
    }
  },
  watch: {
    dimension(newDimension, oldDimension) {
      this.tempViewFace = this.tempView(oldDimension);
      if (newDimension < -2 || newDimension > 2) {
        if (newDimension > oldDimension) {
          this.dimension = -1;
        } else {
          this.dimension = 1;
        }
      }

      setTimeout(() => {
        this.tempViewFace = "";
      }, 350);
    }
  },
  methods: {
    ...mapMutations(["toggleModal", "changeModal"]),
    tempView(oldDimension) {
      switch (oldDimension) {
        case 1:
          return "right";
        case -1:
          return "left";
        case 0:
          return "front";
        default:
          return "back";
      }
    },
    onSwipe(e) {
      switch (e.type) {
        case "swipedown":
          this.swipeDown();
          break;
        case "swipeleft":
          this.swipeLeft(e);
          break;
        case "swiperight":
          this.swipeRight(e);
          break;
      }
    },
    swipeDown() {
      // Close Story Modal
      this.toggleModal();
    },
    swipeRight(e) {
      if (this.activeUserIndex <= 0) {
        // Close Story Modal
        if (this.user.stories.length > 0) {
          return this.changeModal({
            name: "Profile"
          });
        }
        return this.toggleModal();
      }
      this.activeUserIndex -= 1;
      this.dimension -= 1;
      this.rotateY += 90;
    },
    swipeLeft(e) {
      if (this.users.length - 1 <= this.activeUserIndex) {
        // Close Story Modal
        return this.toggleModal();
      }
      this.activeUserIndex += 1;
      this.dimension += 1;
      this.rotateY -= 90;
    }
  },
  async mounted() {
    await this.$nextTick();
    this.activeUserIndex = this.users.findIndex(
      _ => _.userId === this.currentUser.userId
    );
  }
};
</script>

<style lang="scss">
.story-modal {
  height: 100%;
}
</style>