import UserService from "@/services/userService";
import MessageService from "@/services/messageService";
import Vue from "vue";
import Vuex from "vuex";
import guid from "@/utils/guid";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    types: ["Yazı", "Normal"],
    typeOptions: ["Emoji", "Text"],
    activeType: 0,
    activeOption: 1,
    modal: {
      name: null,
      props: {}
    },
    modalVisible: false,
    emojiVisible: false,
    user: {
      stories: []
    },
    size: {
      width: 0,
      height: 0
    },
    users: [],
    options: {}
  },
  getters: {
    types: state => state.types,
    typeOptions: state => state.typeOptions,
    size: state => state.size,
    activeType: state => state.activeType,
    activeOption: state => state.activeOption,
    users: state => state.users,
    options: state => state.options,
    user: state => state.user,
    emojiVisible: state => state.emojiVisible,
    modalVisible: state => state.modalVisible
  },
  mutations: {
    SET_USER(state, payload) {
      state.user = { userId: payload.id || guid(), ...payload };
    },
    SET_USERS(state, payload) {
      state.users = payload.map(u => {
        return {
          userId: u.id || guid(),
          ...u
        };
      });
    },
    REMOVE_STORY(state, id) {
      const index = state.user.stories.findIndex(s => s.id === id);
      state.user.stories.splice(index, 1);
    },
    REMOVE_USER(state, userId) {
      const index = state.users.findIndex(s => s.userId === userId);
      state.users.splice(index, 1);
    },
    SET_STORY(state, payload) {
      state.user.stories.push(payload);
    },
    SET_VIEW(state, payload) {
      const findUser = state.users.find(u => u.userId === payload.userId);
      findUser.stories[payload.storyIndex].viewingUsers.push(state.user.userId);
    },
    setOptions(state, payload) {
      state.options = payload;
    },
    changeType(state, typeIndex) {
      state.activeType = typeIndex;
    },
    changeTypeOption(state, optionIndex) {
      state.activeOption = optionIndex;
    },
    changeModal(state, payload) {
      state.emojiVisible = false;
      state.modalVisible = false;
      state.modal = payload;
      state.modalVisible = true;
    },
    toggleModal: (state, payload) => {
      state.modalVisible = !state.modalVisible;
      if (typeof payload === "function") {
        payload();
      }
    },
    toggleEmoji: state => (state.emojiVisible = !state.emojiVisible),
    closeEmoji: state => (state.emojiVisible = false)
  },
  actions: {
    async getUser({ commit }) {
      const response = await UserService.getUser();

      if (response.status === 200) {
        commit("SET_USER", response.data);
        return response.data;
      }
    },
    async getUsers({ commit }) {
      const response = await UserService.getUsers();

      if (response.status === 200) {
        commit("SET_USERS", response.data);
        return response.data;
      }
    },
    async createStory({ commit, state }, story) {
      // Güncel Servis İçin Değiştirilmesi Gerekir Yeni Story ve Kullanıcı ID Yeterli Olması Gerekir
      const newStory = { id: guid(), ...story };
      const body = {
        ...state.user,
        stories: [...state.user.stories, newStory]
      };
      const response = await UserService.updateUser(body);

      if (response.status === 200) {
        commit("SET_STORY", newStory);
        return response.data;
      }
    },
    async removeStory({ commit, state }, storyId) {
      // Güncel Servis İçin Değiştirilmesi Gerekir Sadece Id Yeterli
      const findIndex = state.user.stories.findIndex(s => s.id === storyId);
      if (findIndex < 0) return false;
      const stories = [...state.user.stories];
      stories.splice(findIndex, 1);
      const response = await UserService.updateUser({
        ...state.user,
        stories
      });

      if (response.status === 200) {
        commit("REMOVE_STORY", storyId);
        return response.data;
      }
    },
    async removeUser({ commit }, userId) {
      const response = await UserService.removeUser(userId);

      if (response.status === 200) {
        commit("REMOVE_USER", userId);
        return response.data;
      }
    },
    async viewUser({ commit, state }, payload) {
      // Güncel Servis İçin Değiştirilmesi Gerekir
      const findUser = state.users.find(u => u.userId === payload.userId);
      if (!findUser) return false;
      const stories = [...findUser.stories];
      stories[payload.storyIndex].viewingUsers.push(state.user.userId);
      const response = await UserService.viewingUser(payload.userId, {
        ...findUser,
        stories
      });

      if (response.status === 200) {
        commit("SET_VIEW", payload);
        return response.data;
      }
    },
    async sendMessage(context, message) {
      const response = await MessageService.sendMessage({
        id: guid(),
        ...message
      });

      if (response.status === 200) {
        // Message Handler
        console.log(response.data);
        return response.data;
      }
    }
  }
});
