<template>
  <div class="profile-modal" v-hammer:swipe.all="onSwipe">
    <Story
      type="profile"
      :storyData="user"
      v-if="user"
      :activeStoryIndex="activeStoryIndex"
      @next="onNext"
      @prev="onPrev"
      @delete="deleteStory"
    />
  </div>
</template>

<script>
import { mapGetters, mapMutations, mapActions } from "vuex";
export default {
  name: "Profile",
  props: {
    activeStoryIndex: {
      type: Number,
      default: 0
    }
  },
  computed: {
    ...mapGetters({
      user: "user",
      users: "users"
    }),
    stories() {
      return this.user.stories;
    }
  },
  methods: {
    ...mapMutations(["toggleModal", "changeModal"]),
    ...mapActions(["removeStory"]),
    onSwipe(e) {
      switch (e.type) {
        case "swipeleft":
          this.onNext();
          break;
        case "swiperight":
          this.onPrev();
          break;
      }
    },
    onNext() {
      this.changeModal({
        name: "Story",
        props: {
          activeUser: this.users[0]
        }
      });
    },
    onPrev() {
      return this.toggleModal();
    },
    deleteStory(id) {
      setTimeout(() => {
        if (this.user.stories.length === 1) {
          this.toggleModal(() => {
            this.removeStory(id);
          });
        } else {
          this.removeStory(id);
        }
      }, 1000);
    }
  }
};
</script>

<style lang="scss">
.profile-modal {
  width: 100%;
  height: 100%;
  color: #fff;
}
</style>