<template>
  <div>
    <img
      v-if="type === 'image'"
      v-show="isLoaded"
      class="media"
      :src="src"
      @load="onLoad"
      @error="onError"
    >
    <video
      v-else-if="type === 'video'"
      v-show="isLoaded"
      class="media"
      :src="src"
      @loadstart="onLoad"
      @loadedmetadata="onLoadMetaData"
      @error="onError"
      ref="video"
      autoplay
      playsinline
      webkit-playsinline
    />
    <Loader v-show="!isLoaded"/>
  </div>
</template>

<script>
import guid from "@/utils/guid";

export default {
  name: "Media",
  props: {
    type: {
      type: String,
      required: true
    },
    src: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      duration: 0,
      isLoaded: false
    };
  },
  computed: {
    srcGuid() {
      return `${this.src}?guid=${guid()}`;
    }
  },
  methods: {
    onError(e) {
      this.$emit("load", e);
    },
    onLoadMetaData() {
      if (this.$refs.video) {
        this.duration = this.$refs.video.duration;
      }
    },
    onLoad(e) {
      this.isLoaded = true;
      this.$emit("load", e);
    }
  }
};
</script>

<style lang="scss" scoped>
.media {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  pointer-events: none;
}
</style>