<template>
  <div v-if="user" class="story" tabindex="0" @keyup.left="prevStory" @keyup.right="nextStory">
    <header class="story__header" v-show="!full">
      <Button v-if="!isMobile" type="circle" class="story__navigation -prev" @click="prevStory">
        <Icon name="ChevronLeft"/>
      </Button>
      <Button v-if="!isMobile" type="circle" class="story__navigation -next" @click="nextStory">
        <Icon name="ChevronRight"/>
      </Button>
      <Button class="navigation -next"></Button>
      <Progress
        :count="stories.length"
        :time="story && story.duration ? story.duration : settings.duration"
        :pause="pause"
        :activeNumber="activeStoryItemIndex + 1"
        @end="onEnd"
      />
      <div class="story__info">
        <CircleImg v-if="user.photo" :src="user.photo" :title="user.name"/>
        <span class="story__title">{{ user.name }}</span>
        <Close class="story__close"/>
      </div>
    </header>
    <div
      class="story__content"
      v-hammer:press="onPress"
      v-hammer:pressup="onPressUp"
      @mousedown="onPress"
      @mouseup="onPressUp"
      v-hammer:tap="onTab"
    >
      <transition enter-active-class="fadeIn" leave-active-class="fadeOut">
        <Label v-if="isMessageSend" class="story__notification">Mesajınız gönderildi</Label>
      </transition>
      <Emoji title="Hızlı Cevap" class="story__emoji" @selectEmoji="sendEmoji"/>
      <StoryItem :story="story" @load="loadMedia"/>
    </div>
    <footer class="story__footer" v-show="!full" v-if="!$slots.footer && type !== 'profile'">
      <Button type="circle" size="md" class="-m-right-10">
        <File accept="image/*" @change="replyPhoto">
          <Icon name="Camera"/>
        </File>
      </Button>
      <InputText
        placeholder="Mesaj Gönder"
        v-model="message"
        @keyup.enter="messageHandler"
        @focus="onFocus"
        @blur="onFocus"
      >
        <Select @visible="(s) => complainBoxVisible = s">
          <template slot="label">
            <Icon name="More"/>
          </template>
        </Select>
      </InputText>
      <Button class="-m-left-10" @click="messageHandler">
        <Icon name="Send"/>
      </Button>
    </footer>
    <footer class="story__footer" v-else-if="!$slots.footer && type === 'profile'">
      <Button @click="onViewingUser">
        <Icon name="Group" class="-m-right-15"/>
        {{ story && story.viewingUsers ? story.viewingUsers.length : 0 }} Kişi gördü
      </Button>
      <Button class="-position-right" @click="$emit('delete', story.id)">
        <Icon name="Bin"/>
      </Button>
    </footer>
    <footer class="story__footer" v-show="!full" v-else>
      <slot name="footer"/>
    </footer>
  </div>
</template>

<script>
import { mapMutations, mapGetters, mapActions } from "vuex";

export default {
  name: "Story",
  props: {
    storyData: {},
    activeStoryIndex: {
      type: Number,
      default: 0
    },
    type: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      activeStoryItemIndex: this.activeStoryIndex,
      message: "",
      isMessageSend: false,
      focus: false,
      pressed: false,
      full: false,
      loadedMedia: false,
      complainBoxVisible: false
    };
  },
  computed: {
    user() {
      return this.storyData;
    },
    stories() {
      if (!this.user) return [];
      return this.user.stories;
    },
    story() {
      return this.stories[this.activeStoryItemIndex];
    },
    pause() {
      return (
        this.focus ||
        this.pressed ||
        this.full ||
        !this.loadedMedia ||
        this.complainBoxVisible
      );
    }
  },
  watch: {
    message(m) {
      if (m.length > 0) {
        this.closeEmoji();
      }
    }
  },
  methods: {
    ...mapMutations(["changeModal", "toggleEmoji", "closeEmoji"]),
    ...mapActions(["sendMessage", "viewUser"]),
    onFocus(e) {
      const isFocus = e.type !== "blur";
      if (isFocus && this.message.length === 0) {
        this.focus = true;
        this.toggleEmoji();
      } else {
        this.focus = false;
      }
    },
    onEnd() {
      this.nextStory();
    },
    onPress(e) {
      this.pressed = true;
    },
    onPressUp(e) {
      this.pressed = false;
    },
    onTab(e) {
      if (!this.isMobile || this.pause) return false;
      const screenWidth = this.$el.clientWidth / 2;
      if (e.center.x >= screenWidth) {
        this.nextStory();
      } else {
        this.prevStory();
      }
    },
    onViewingUser() {
      this.changeModal({
        name: "Viewing",
        props: {
          storyIndex: this.activeStoryItemIndex
        }
      });
    },
    loadMedia(e) {
      if (e.type === "error") {
        this.nextStory();
      } else {
        this.loadedMedia = true;
      }
    },
    async nextStory() {
      await this.viewUser({
        userId: this.user.userId,
        storyIndex: this.activeStoryItemIndex
      });
      if (this.stories.length - 1 > this.activeStoryItemIndex) {
        this.activeStoryItemIndex += 1;
      } else {
        this.$emit("next");
      }
    },
    prevStory() {
      if (this.activeStoryItemIndex > 0) {
        this.activeStoryItemIndex -= 1;
      } else {
        this.$emit("prev");
      }
    },
    async sendEmoji(emoji) {
      this.isMessageSend = true;
      await this.sendMessage({
        message: emoji,
        id: this.story.id,
        userId: this.user.userId
      });
      setTimeout(() => {
        this.isMessageSend = false;
      }, 2000);
      this.toggleEmoji();
    },
    async messageHandler() {
      if (this.message.trim().length === 0) return false;
      this.isMessageSend = true;

      await this.sendMessage({
        message: this.message,
        id: this.story.id,
        userId: this.user.userId
      });
      setTimeout(() => {
        this.isMessageSend = false;
      }, 2000);
      this.message = "";
      this.closeEmoji();
    },
    replyPhoto(files) {
      const file = files[0];
      this.changeModal({
        name: "Reply",
        props: {
          selectedFile: file,
          story: this.user,
          storyItem: this.story,
          storyItemIndex: this.activeStoryItemIndex
        }
      });
    }
  }
};
</script>

<style lang="scss">
