  width: 100%;
  height: 100%;
  position: relative;
  z-index: 1;
  display: flex;
  flex-flow: column;
  align-content: stretch;
  justify-content: space-between;

  &:hover & {
    &__navigation {
      opacity: 1;
    }
  }

  &__header {
    padding: 0 15px;
    background: -moz-linear-gradient(
      top,
      rgba(0, 0, 0, 0.63) 0%,
      rgba(0, 0, 0, 0) 100%
    ); /* FF3.6-15 */
    background: -webkit-linear-gradient(
      top,
      rgba(0, 0, 0, 0.63) 0%,
      rgba(0, 0, 0, 0) 100%
    ); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0.63) 0%,
      rgba(0, 0, 0, 0) 100%
    ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#a1000000', endColorstr='#00000000',GradientType=0 ); /* IE6-9 */
  }

  &__progress {
    display: flex;
    margin-bottom: 10px;
  }

  &__info {
    display: flex;
    align-items: center;
    font-size: 18px;
    font-weight: 700;
    margin-top: 10px;
  }

  &__title {
    margin-left: 15px;
    font-weight: normal;
  }

  &__content {
    height: 100%;
  }

  &__emoji {
    z-index: 2;
  }

  &__notification {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  &__close {
    margin-left: auto;
    margin-right: 0;
  }

  &__navigation {
    opacity: 0;
    transition: opacity 1s ease-in-out;
    position: absolute;
    top: 50%;
    z-index: 99;

    &.-prev {
      left: 15px;
    }

    &.-next {
      right: 15px;
    }
  }

  &__footer {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 15px 15px;
    min-height: 30px;
    display: flex;
    align-items: center;
    z-index: 999;
    background: -moz-linear-gradient(
      top,
      rgba(0, 0, 0, 0) 0%,
      rgba(0, 0, 0, 0.6) 100%
    ); /* FF3.6-15 */
    background: -webkit-linear-gradient(
      top,
      rgba(0, 0, 0, 0) 0%,
      rgba(0, 0, 0, 0.6) 100%
    ); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0) 0%,
      rgba(0, 0, 0, 0.6) 100%
    ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#99000000',GradientType=0 ); /* IE6-9 */
  }
}
</style>
