<template>
  <Tab :tabs="tabs" @changeTab="changeType" class="reply-mode">
    <!-- Tab Header -->
    <template slot="header">
      <OptionsBar/>
    </template>
    <!-- Tab Content -->
    <Media :src="file.src" class="reply-mode__media" ref="media" :type="file.type"/>
    <TextItem
      v-model="txt"
      :color="color"
      :bg="higlight"
      ref="text"
      vertical="top"
      :font="selectedFont.font"
      @focus="focusText"
    />
    <Emoji @selectEmoji="selectEmoji"/>
    <!-- Tab Footer -->
    <template slot="footer">
      <ColorSetting v-model="color" v-if="isFocus"/>
      <ActionsBar v-if="!isFocus" @action="action">
        <Button @click="action" class="-position-center" column>
          <CircleImg :src="story.photo" :title="story.name"/>
          <span>{{story.name}}</span>
        </Button>
      </ActionsBar>
    </template>
  </Tab>
</template>

<script>
import { mapMutations, mapGetters, mapActions } from "vuex";

export default {
  name: "ReplyStory",
  props: {
    selectedFile: {
      type: File
    },
    story: null,
    storyItem: null,
    storyItemIndex: null
  },
  data() {
    return {
      tabs: ["TEKRAR OYNAT", "BİR KEZ GÖRÜNTÜLE"],
      currentTab: 0,
      file: {
        src: "/img/story.png",
        type: "image"
      },
      txt: "",
      color: "#fff",
      font: null,
      isFocus: false,
      higlight: "#0e277c",
      textPosition: {
        y: 100
      }
    };
  },
  computed: {
    ...mapGetters({
      optionIndex: "activeOption",
      user: "user"
    })
  },
  methods: {
    ...mapMutations(["closeEmoji", "changeModal"]),
    ...mapActions(["sendMessage"]),
    selectEmoji(emoji) {
      this.txt += emoji;
      this.closeEmoji();
    },
    focusText(status) {
      setTimeout(() => {
        this.isFocus = status;
      }, 100);
    },
    fileChangeHandler() {
      const files = this.selectedFile;
      let file = null;
      if (Array.isArray(files)) {
        file = files[0];
      } else {
        file = files;
      }
      if (file.type.match("image.*|video.*")) {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = evt => {
          if (evt.target.readyState == FileReader.DONE) {
            this.file = {
              src: evt.target.result,
              type: file.type.match("image.*") ? "image" : "video"
            };
          }
        };
      }
    },
    async action() {
      const items = [];

      if (this.txt) {
        const text = this.$refs.text;
        items.push({
          type: "text",
          value: this.txt,
          vertical: text.vertical,
          customProperties: {
            color: this.color,
            font: this.selectedFont.font,
            bg: this.higlight
          }
        });
      }

      if (this.emoji) {
        const emoji = this.$refs.emoji;
        items.push({
          type: "emoji",
          value: this.emoji,
          position: emoji.coordinat,
          rotate: emoji.rotation,
          customProperties: []
        });
      }

      const result = await this.sendMessage({
        message: this.file.src,
        id: this.storyItem.id,
        userId: this.user.userId,
        type: this.tabs[this.currentTab]
      });

      this.changeModal({
        name: "Story",
        props: {
          activeUser: this.story,
          activeStoryIndex: this.storyItemIndex
        }
      });
    },
    changeType(index) {
      this.currentTab = index;
    }
  },
  async mounted() {
    await this.$nextTick();
    this.fileChangeHandler();
  }
};
</script>

<style lang="scss">
.reply-mode {
  position: relative;
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  padding-top: 100px;

  &__media {
    z-index: 0 !important;
  }

  &__footer {
    position: absolute;
    bottom: 50px;
    left: 0;
    right: 0;
  }
}
</style>