import Api from "@/services/api";

export default {
  async getUser(params) {
    const { userPath } = Api.paths;
    return await Api.Service.get(userPath, {
      params
    });
  },
  async getUsers(params) {
    const { usersPath } = Api.paths;
    return await Api.Service.get(usersPath, {
      params
    });
  },
  async viewingUser(userId, body) {
    const { usersPath } = Api.paths;
    return await Api.Service.put(`${usersPath}/${userId}`, body);
  },
  async removeUser(userId) {
    const { usersPath } = Api.paths;
    return await Api.Service.delete(`${usersPath}/${userId}`);
  },
  async updateUser(body) {
    const { userPath } = Api.paths;
    return await Api.Service.put(userPath, body);
  }
};
