<template>
  <div class="viewing-modal">
    <Tab :tabs="tabs" type="story" @changeTab="changeType" :activeIndex="activeTabIndex">
      <template slot="header">
        <Close class="viewing-modal__close" fill="#222222" :closeMethod="closeModal"/>
      </template>
      <!-- Tab Content -->
      <h2 class="viewing-modal__title">Görüntüleyenler</h2>
      <div class="viewing-modal__list">
        <transition-group tag="div" enter-active-class="fadeIn" leave-active-class="fadeOut">
          <Card
            v-for="user in viewingUsers"
            :key="user.userId"
            :title="user.userName"
            :desc="user.name"
            :image="user.photo"
          >
            <template slot="actions">
              <Button @click="removeViewer(user.userId)">
                <Icon name="Bin"/>
              </Button>
            </template>
          </Card>
        </transition-group>
      </div>
      <!-- /Tab Content -->
    </Tab>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";

export default {
  name: "ViewingModal",
  props: {
    storyIndex: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      activeTabIndex: this.storyIndex
    };
  },
  computed: {
    ...mapGetters({
      user: "user",
      users: "users"
    }),
    stories() {
      return this.user.stories;
    },
    tabs() {
      return this.stories;
    },
    activeStory() {
      return this.stories[this.activeTabIndex];
    },
    viewingUsers() {
      if (!this.activeStory || !this.activeStory.viewingUsers) return [];
      return this.activeStory.viewingUsers.reduce((acc, userId) => {
        const findUser = this.users.find(u => u.userId === userId);
        if (findUser) {
          return acc.concat({
            userId: findUser.userId,
            name: findUser.name,
            userName: findUser.userName,
            photo: findUser.photo
          });
        }
        return acc;
      }, []);
    }
  },
  methods: {
    ...mapMutations(["changeModal"]),
    changeType(index) {
      this.activeTabIndex = index;
    },
    removeViewer(id) {
      // TODO
    },
    closeModal() {
      this.changeModal({
        name: "Profile",
        props: {
          activeStoryIndex: this.activeTabIndex
        }
      });
    }
  }
};
</script>

