.viewing-modal {
  height: 100%;

  &__title {
    margin-bottom: 24px;
  }

  &__list {
    overflow: auto;
    height: 100%;
  }

  &__close {
    position: absolute;
    right: 15px;
    top: 20px;
  }
}
</style>
