<template>
  <div :class="variants">
    <div class="tab__header" v-if="$slots.header || type === 'story'">
      <nav v-if="type === 'story'" v-hammer:swipe.all="onSwipe" class="tab__nav" :style="navStyles">
        <Button v-for="(tab, i) in tabs" :key="i" @click="activeTabIndex = i">
          <StoryItem
            :story="tab"
            class="tab__btn -media"
            :class="i === activeTabIndex ? '-active' : ''"
            :viewingUserVisible="true"
            ref="tabBtn"
          />
        </Button>
      </nav>
      <slot name="header"/>
    </div>
    <div class="tab__content">
      <slot/>
    </div>
    <div class="tab__footer">
      <slot name="footer" v-if="$slots.footer"/>
      <nav v-if="!type" v-hammer:swipe.all="onSwipe" class="tab__nav" :style="navStyles">
        <Button
          v-for="(tab, i) in tabs"
          :key="i"
          :class="i === activeTabIndex ? 'tab__btn -active' : 'tab__btn'"
          ref="tabBtn"
          @click.prevent="activeTabIndex = i"
        >{{ tab }}</Button>
      </nav>
    </div>
  </div>
</template>

<script>
export default {
  name: "Tab",
  props: {
    tabs: {
      type: Array
    },
    type: {
      type: String
    },
    activeIndex: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      activeTabIndex: this.activeIndex,
      tabNavPos: 0
    };
  },
  computed: {
    navStyles() {
      return {
        transform: `translateX(${this.tabNavPos}px)`
      };
    },
    variants() {
      return {
        tab: true,
        [`-${this.type}`]: !!this.type
      };
    }
  },
  watch: {
    activeTabIndex(index) {
      this.setTabNavPosition();
      this.$emit("changeTab", index);
    }
  },
  methods: {
    setTabNavPosition() {
      const currentNav = this.$refs.tabBtn[this.activeTabIndex];
      if (!currentNav) return;
      const $currentNav = currentNav.$el;
      const centerPos = this.$el.clientWidth / 2;
      const navWidth = $currentNav.clientWidth;
      this.tabNavPos = centerPos - $currentNav.offsetLeft - navWidth / 2;
    },
    onSwipe(e) {
      switch (e.type) {
        case "swipeleft":
          if (this.activeTabIndex < this.tabs.length - 1) {
            this.activeTabIndex += 1;
          }
          break;
        case "swiperight":
          if (this.activeTabIndex > 0) {
            this.activeTabIndex -= 1;
          }
          break;
      }
    }
  },
  async mounted() {
    await this.$nextTick();
    this.setTabNavPosition();
  }
};
</script>

