<template>
  <div class="select">
    <div class="select__toggle">
      <Button @click="isVisible = !isVisible">
        <slot name="label"/>
      </Button>
    </div>
    <div class="select__dropdown" v-if="isVisible">
      <Button class="select__item -c-red">Şikayet Et</Button>
      <Button class="select__item" @click="close">İptal</Button>
    </div>
  </div>
</template>

<script>
export default {
  name: "Select",
  data() {
    return {
      isVisible: false
    };
  },
  watch: {
    isVisible(status) {
      this.$emit("visible", status);
    }
  },
  methods: {
    close() {
      setTimeout(() => {
        this.isVisible = false;
      }, 100);
    }
  }
};
</script>

