.select {
  position: relative;
  &__dropdown {
    position: fixed;
    left: 15px;
    bottom: 15px;
    width: calc(100% - 30px);
    border-radius: 28.5px;
    overflow: hidden;
  }

  &__item {
    display: flex;
    height: 56px;
    width: 100%;
    background-color: #ffffff;
    color: $dark-blue;

    & + & {
      margin-left: 0;
      border-top: 1px solid #dfdfdf;
    }

    &.-c-red {
      color: $red;
    }

    &:hover {
      background-color: rgba(255, 255, 255, 0.9);
    }
  }
}
</style>